import MaterialTable from '@material-table/core';
import { Button, Grid, Paper, TableContainer, Typography, Dialog, DialogActions, DialogTitle, DialogContent, Divider } from '@mui/material';
import makeStyles from '@mui/styles/makeStyles';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import React, { useContext, useEffect, useState } from 'react';
import { getAllOrder, updateOrder } from '../../../action/orderAction';
import LayoutAdmin from '../../../component/LayoutAdmin/LayoutAdmin';
import { OrderAdminContext } from '../../../contextAPI/OrderAdminContext';
import NumberFormat from 'react-number-format';

const useStyles = makeStyles(theme => ({
    title: {
        fontWeight: '500',
        marginBottom: theme.spacing(4)
    },
    table: {
        display: 'block',
        width: '100%',
        backgroundColor: 'white',
        marginTop: '18px',
    },
    icon: {
        cursor: 'pointer',
        marginRight: theme.spacing(1),
    },
    flexContainer: {
        display: 'flex'
    },
    flexItem: {
        width: '50%'
    },
    divider: {
        margin: '8px 0'
    },
    dialog: {
        marginTop: 8
    }
}))

const getDate = (date) => {
    let newDate = new Date(date);
    return newDate.toLocaleString()
}

const AOrder = () => {
    const classes = useStyles();
    const { orderAdmin, orderAdminDispatch } = useContext(OrderAdminContext);
    const [open, setOpen] = useState(false);
    const [orderDetail, setOrderDetail] = useState(null)

    const handleSubmit = (data) => {
        console.log(data);
        if (getOrderStatus(data.orderStatus) == "delivered") {
            setOrderDetail(data);
            setOpen(true)
        } else
            updateOrder(orderAdminDispatch, data)
    }

    const handleClose = () => {
        setOpen(false)
    }

    const getStatus = (stt) => {
        const statusObj = {
            ordered: " t hng",
            packed: " xc nhn",
            shipped: "ang giao",
            delivered: " giao hng",
        }
        return statusObj[stt]
    }

    const getStep = (s) => {
        const step = {
            ordered: "Xc nhn",
            packed: "Giao hng",
            shipped: "Xc nhn  giao",
            delivered: "Xem chi tit"
        }
        return step[s]
    }

    const getOrderStatus = (data) => {
        let status;
        data.map(el => {
            if (el.isCompleted)
                status = el.type
        })
        return status;
    }

    const getColor = (color) => {
        const statusObj = {
            ordered: "#FFE800",
            packed: "orange",
            shipped: "blue",
            delivered: "green"
        }
        return statusObj[color]
    }

    const [columns] = useState([
        { title: 'STT', field: 'id', width: 20 },
        { title: 'Khch hng', field: 'fullName', width: 500 },
        {
            title: 'n hng', field: 'items', width: 700,
            render: (params) => {
                return (
                    <>
                        {params.items.map((el, index) =>
                            <Grid container key={index} >
                                <Grid item flex={1}>
                                    {el.productId.name}
                                </Grid>
                                <Grid item sx={2}>
                                    x {el.quantity}
                                </Grid>
                            </Grid>
                        )}
                    </>
                )
            }
        },
        { title: "Email", field: "email" },
        {
            title: "S in thoi", field: "phoneNumber",
            render: (params) => params.address.phoneNumber
        },
        {
            title: 'Tng', field: 'totalAmount', width: 130, align: 'right',
            render: (params) => <NumberFormat value={params.totalAmount} displayType="text" thousandSeparator={true} suffix="" />
        },
        {
            title: 'a ch', field: 'address', width: 500,
            render: (params) => params.address.address + ", " + params.address.district + ", " + params.address.province
        },
        { title: 'Ngy t', field: 'createdAt', width: 150, align: 'right' },
        {
            title: 'Trng thi', field: 'paymentStatus', width: 140,
            render: (params) => {
                if (params.paymentStatus == "cancelled")
                    return (
                        <span style={{ color: "red" }}>
                             hy
                        </span>
                    )
                else
                    return (
                        <span style={{ color: getColor(getOrderStatus(params.orderStatus)) }}>
                            {getStatus(getOrderStatus(params.orderStatus))}
                        </span>
                    )
            }
        },
        {
            title: 'X l', field: 'action', minWidth: 150,
            render: (params) => {
                return (
                    <Button
                        disabled={params.paymentStatus == "cancelled"}
                        onClick={() => handleSubmit(params)}
                        size="small"
                        variant="contained" color="info"
                    >
                        {getStep(getOrderStatus(params.orderStatus))}
                    </Button>
                )
            }
        },
    ]);
    const [rows, setRows] = useState(orderAdmin.orders.map((order, index) => {
        const { paymentStatus, totalAmount, _id, items, createdAt, user, orderStatus, address } = order;
        return {
            id: index + 1,
            _id,
            items,
            phoneNumber: user.phoneNumber,
            address: address,
            orderStatus,
            fullName: user.fullName,
            email: user.email,
            totalAmount,
            createdAt: getDate(createdAt),
            paymentStatus
        };
    }));

    
    // useEffect(() => {
    //     getAllOrder(orderAdminDispatch);
    // }, [])

    // useEffect(() => {
    //     const list = orderAdmin.orders.map((order, index) => {
    //         console.log
    //         const { paymentStatus, totalAmount, _id, items, createdAt } = order;
    //         return {
    //             id: index + 1,
    //             items,
    //             totalAmount,
    //             paymentStatus: paymentStatus === "pending" && "ang ch"
    //         };
    //     })
    //     setRows(list);
    // }, [])

    return (
        <LayoutAdmin sidebar>
            <Typography className={classes.title} variant="h3" color="primary">n hng</Typography>
            <TableContainer className="order-table" style={{ maxWidth: "1170px", }} >
                <MaterialTable
                    components={{
                        Container: (props) => <Paper
                            {...props}
                            className={classes.table}
                            variant="outlined"
                        />
                    }}
                    title={""}
                    columns={columns}
                    data={rows}
                    options={{
                        padding: 'normal'
                    }}
                    isLoading={orderAdmin.loading}
                />
            </TableContainer>
            {orderDetail &&
                <Dialog PaperProps={{ style: { minWidth: '900px' } }} fullWidth open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">Thng tin n hng</DialogTitle>
                    <DialogContent >
                        <Grid container spacing={3} className={classes.dialog}>
                            <Grid item xs={6} container spacing={1}>
                                <b>Thng tin khch hng</b>
                                <Grid item xs={12} container >
                                    <Grid item xs={5}>
                                        H tn
                                    </Grid>
                                    <Grid item xs={7}>
                                        {orderDetail.fullName}
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} container>
                                    <Grid item xs={5}>
                                        Email
                                    </Grid>
                                    <Grid item xs={7}>
                                        {orderDetail.email}
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Divider className={classes.divider} />
                                </Grid>
                                <b>Thng tin ngi nhn hng</b>
                                <Grid item xs={12} container >
                                    <Grid item xs={5}>
                                        H tn
                                    </Grid>
                                    <Grid item xs={7}>
                                        {orderDetail.address.name}
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} container>
                                    <Grid item xs={5}>
                                        Email
                                    </Grid>
                                    <Grid item xs={7}>
                                        {orderDetail.email}
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} container>
                                    <Grid item xs={5}>
                                        S in thoi
                                    </Grid>
                                    <Grid item xs={7}>
                                        {orderDetail.address.phoneNumber}
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} container>
                                    <Grid item xs={5}>
                                        a ch
                                    </Grid>
                                <Grid item xs={7}>
                                        {orderDetail.address.address + ", " + orderDetail.address.district + ", " + orderDetail.address.province}
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Divider className={classes.divider} />
                                </Grid>
                                <b>Thng tin n hng</b>
                                {
                                    orderDetail.items.map((el, index) =>
                                        <Grid item xs={12} key={index} container>
                                            <Grid item flex={1}>
                                                {el.productId.name}
                                            </Grid>
                                            <Grid item xs={2}>
                                                x {el.quantity}
                                            </Grid>
                                            <Grid item xs={3}>
                                                <NumberFormat value={el.price} displayType="text" thousandSeparator={true} suffix="" />
                                            </Grid>
                                        </Grid>
                                    )
                                }
                                <Grid item xs={12} container>
                                    <Grid item flex={1}>
                                        Tng
                                    </Grid>
                                    <Grid item xs={3}>
                                        <NumberFormat value={orderDetail.totalAmount} displayType="text" thousandSeparator={true} suffix="" />
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={6} container spacing={1}>
                                <b>Nht k giao hng</b>
                                {
                                    orderDetail.orderStatus.map((el, index) =>
                                        <Grid item xs={12} key={index} container>
                                            <Grid item xs={5}>
                                                {getStatus(el.type)}
                                            </Grid>
                                            <Grid item xs={7}>
                                                {getDate(el.date)}
                                            </Grid>
                                        </Grid>
                                    )
                                }
                            </Grid>
                        </Grid>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color="primary" >
                            Thot
                        </Button>
                    </DialogActions>
                </Dialog>
            }
        </LayoutAdmin>
    );
};

export default AOrder;